//import scrollTo from 'react-scroll-to-component'

class Nav extends React.Component {

	constructor(props) {
		super(props)

		this.handleClick = this.handleClick.bind(this)
	}

	componentDidMount() {
		this.scrollTo = require('react-scroll-to-component')
	}

	setActiveState() {
		//TODO set active steate for selected menu item
	}

	handleClick(event, link) {
		console.log('clicked')
		console.log(event)
		console.log(link)
		this.props.changeSection.bind(null, link.href)
	}

	render () {
		return (
			<nav className="main-nav">
					{this.props.links.map((link, i) => {
						return (
							<a className="main-nav__link"
							   key={i}
							   href={link.href}
								onClick={(event) => {
									event.preventDefault()
									this.handleClick(link)
								}}>
							   {link.name}
							</a>
						)
					})}
			</nav>
		)
	}

}

export default Nav
